@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
@font-face {font-family: "Brfirma-Regular"; src: url(/static/media/BRFirma-Regular.f41e4eb1.ttf); }

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  /* ///// 1a. Colors /////// */
  /* Main Colors */
  --light-orange: #fd8b003a;
  --molecular-orange: #fd8c00;
}
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  color: #150538;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; /* Hide horizontal scrollbar */
  scroll-behavior: smooth;
  height: 100%;
  background-color: #ffffff !important;
  background: #ffffff;
}
@media (max-width: 700px) {
  body {
    font-size: 14px;
  }
}
hr {
  margin: 1.2rem 0 !important;
}
html {
  scroll-behavior: smooth;
}
/* width */
::-webkit-scrollbar {
  width: 1px;
  height: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #9a9a9c;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #32335a;
}
.newitem {
  overflow-y: scroll;
  min-height: 500px;
  max-height: 525px;
}

a,a:hover{
  text-decoration: none;
}
 select ::-webkit-input-placeholder{
  color: #000000 !important;
}
 select :-ms-input-placeholder{
  color: #000000 !important;
}
 select ::placeholder,select{
  color: #000000 !important;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


@media (max-width: 460px) {
  .whatsapp {
    width: 40px;
    position: fixed;
    bottom: 2rem;
    left: 1rem;
    z-index: 100;
  }
}
@media(min-width:461px){
  .whatsapp{
    display: none;
  }
}
.App{
  overflow-x: hidden;
}
*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  font-family: "DM Sans", sans-serif;
  line-height: 1.5;
  background-color: #f1f3fb;
  padding: 0 2rem;
}

img {
  max-width: 100%;
  display: block;
}

input {
  -webkit-appearance: none;
  appearance: none;
  border-radius: 0;
}

.card {
  margin: 2rem auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  max-width: 425px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 10px 20px 0 rgba(153, 153, 153, 0.25);
  padding: 0.75rem;
}

.card-image {
  border-radius: 8px;
  overflow: hidden;
  padding-bottom: 65%;
  background: linear-gradient(
      to bottom,
      rgba(29, 29, 29, 0.5),
      rgba(20, 39, 23, 0.3)
    ),
    url(/static/media/coffee_3.08686982.jpeg);
  background-size: contain;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 5%;
  position: relative;
}

.card-heading {
  position: absolute;
  left: 10%;
  top: 15%;
  right: 10%;
  font-size: 1.75rem;
  font-weight: 700;
  color: #fff;
  line-height: 1.222;
}
.card-heading small {
  display: block;
  font-size: 0.75em;
  font-weight: 400;
  margin-top: 0.25em;
}

.card-form {
  padding: 2rem 1rem 0;
}

.input {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
  position: relative;
  padding-top: 1.5rem;
}
.input + .input {
  margin-top: 1.5rem;
}

.input-label {
  color: #8597a3;
  position: absolute;
  top: 1.5rem;
  transition: 0.25s ease;
}

.input-field {
  border: 0;
  z-index: 1;
  background-color: transparent;
  border-bottom: 2px solid #eee;
  font: inherit;
  font-size: 1.125rem;
  padding: 0.25rem 0;
}
.input-field:focus,
.input-field:valid {
  outline: 0;
  border-bottom-color: #735400;
}
.input-field:focus + .input-label,
.input-field:valid + .input-label {
  color: #735400;
  -webkit-transform: translateY(-1.5rem);
          transform: translateY(-1.5rem);
}

.action {
  margin-top: 2rem;
}

.action-button {
  font: inherit;
  font-size: 1.25rem;
  padding: 1em;
  width: 100%;
  font-weight: 500;
  border: 1px solid #735400;
  border-radius: 6px;
  color: #fff;
  color: #735400;
}
.action-button:focus {
  outline: 0;
}

.card-info {
  padding: 1rem 1rem;
  text-align: center;
  font-size: 0.875rem;
  color: #8597a3;
}
.card-info a {
  display: block;
  color: #735400;
  text-decoration: none;
}
.jcenter {
  padding-top: 2rem;
  -webkit-justify-content: center;
          justify-content: center;
}
.card2 {
  border-radius: 200px;
  height: 200px;
